<template>
	<div class="app-content">
		<div class="container">
			<div class="page-title">
				<h1>Товары</h1>
				<div class="actions">
					<router-link to="/new" class="btn btn-success">
						<span class="fas fa-plus"></span>
						Добавить новый
					</router-link>
				</div>
			</div>
			<div class="page-content">
				<div class="input-group">
					<span class="input-group-text" id="basic-addon1">Поиск</span>
					<input type="text" class="form-control" placeholder="Наименование товара" v-model="query">
				</div>
				<div class="products-table">
					<div v-if="!filteredProducts.length" class="alert alert-danger" role="alert">Данный товар не найден</div>
					<table v-else class="table table-hover border">
						<thead>
							<tr>
								<th>#</th>
								<th>Артикул</th>
								<th>Фото</th>
								<th>Наименование</th>
								<th>Стоимость</th>
								<th>Время добавления</th>
								<th>Действия</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(product, index) in filteredProducts[paginationSelectedPage]" :key="index">
								<td>{{ index + 1}}</td>
								<td class="code">{{ product.code }}</td>
								<td class="image">
									<img :src="product.image">
								</td>
								<td class="items">
									<span class="title">{{ product.name['ua'] }}</span>
								</td>
								<td class="cost">{{ product.cost }}</td>
								<td class="time">{{ product.date }}</td>
								<td width="0">
									<router-link class="btn btn-primary" :to="'/products/' + product.code">Перейти</router-link>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
				<nav>
					<ul class="pagination justify-content-end">
						<li class="page-item" :class="{'disabled': paginationSelectedPage == 0}"><a class="page-link" @click="paginationController(paginationSelectedPage - 1)">Назад</a></li>

						<li class="page-item" :class="{'active': (index == paginationSelectedPage)}"  v-for="(page, index) in filteredProducts" :key="index" @click="paginationController(index)"><a class="page-link">{{ index + 1 }}</a></li>

						<li class="page-item" :class="{'disabled': paginationSelectedPage == filteredProducts.length - 1}"><a class="page-link" @click="paginationController(paginationSelectedPage + 1)">Вперед</a></li>
					</ul>
				</nav>
			</div>
		</div>
	</div>
</template>

<script>
export default {
    data() {
        return {
            query: '',
            paginationSelectedPage: 0,
            productsPerPage: 12
        }
    },
    computed: {
        products() {
            return this.$store.getters['getProducts'].sort((a, b) => {
                return b.code - a.code;
            });
        },
        filteredProducts() {
            var index = 0;
            var result = [[]];
            var product = (!this.query.length)
                ? this.products
                : this.products.filter((product) => {
                    return ~product.name['ua'].toLowerCase().indexOf(this.query.toLowerCase())
                        || ~product.code.toLowerCase().indexOf(this.query.toLowerCase());
                });

            for (var i = 0; i < product.length; i++) {
                if (i % this.productsPerPage == 0 && i != 0) {
                    index++;
                    result[index] = [];
                }
                result[index].push(product[i]);
            }
            return result;
        }
    },
    methods: {
        paginationController (index) {
            this.paginationSelectedPage = index;
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
        }
    },
    mounted: function () {
        
    }
};
</script>